Vue.prototype.baseURI = `http://127.0.0.1:8080/parkingLot`
import 'element-ui/lib/theme-chalk/index.css';
import axios from 'axios' 

Vue.config.productionTip = false
Vue.prototype.axios=axios;
Vue.use(ElementUI); 
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



